import React, { useEffect, useRef, useState } from "react";
import { throttle } from "throttle-debounce";
import AnimatedNumber from "animated-number-react";

const App = ({ data }) => {
  const [scrollCheck, setScrollCheck] = useState(false);
  const element = useRef();
  const scrollYCheck = () => {
    const { current } = element;
    const elementCoordinate = current ? current.getBoundingClientRect().top : 0;
    if (elementCoordinate + 300 < window.innerHeight) {
      setScrollCheck(true);
    } else {
      setScrollCheck(false);
    }
  };
  const throttleScrollYCheck = throttle(200, scrollYCheck);
  useEffect(() => {
    window.addEventListener("scroll", throttleScrollYCheck);
    return () => window.removeEventListener("scroll", throttleScrollYCheck);
  });

  return (
    <>
      <section className="py-20 bg-gray-900" ref={element}>
        <div className=" w-11/12 mx-auto  text-center">
          <h1 className="flex justify-center items-center text-2xl font-bold  text-white leading-normal my-12">
            {data.title}
          </h1>
          <ul className="flex flex-col justify-center items-center text-white my-12">
            {data.contents.map((conVal, conIdx) => (
              <>
                <li className="w-44">
                  <p className=" text-orange-500 font-bold text-2xl my-2">
                    <AnimatedNumber
                      value={scrollCheck && conVal.data}
                      formatValue={(val) => val.toFixed(conIdx < 2 ? 0 : 1)}
                      duration={1500}
                    ></AnimatedNumber>
                    {conVal.unit}
                  </p>
                  <h2 className="text-gray-300 mb-3">{conVal.title}</h2>
                </li>
              </>
            ))}
          </ul>
        </div>
      </section>
    </>
  );
};

export default App;
